<template>
  <!-- <el-dialog> -->
    <el-dialog title="编辑" width="500px" :visible.sync="dialogTableVisible">
        <div class="dialog-content">
            <div class="list">
                <span class="require"><i>用户姓名：</i></span>
                <span class="text">{{name}}</span>
            </div>
            <div class="list">
                <span class="require"><i>邀请人姓名：</i></span>
                <span class="text">{{invitName}}</span>
            </div>
            <div class="list">
                <span class="require"><i>邀请状态：</i></span>
                <el-select class="com-input" v-model="roleId" placeholder="请选择邀请状态">
                    <el-option v-for="item in invitList" :key="item.id" :label="item.name" :value="item.dValue" />
                </el-select>
            </div>
            <div class="list">
                <span class="require"><i>成交类型：</i></span>
                <el-select class="com-input" v-model="roleId" placeholder="请选择成交类型">
                    <el-option v-for="item in dealList" :key="item.id" :label="item.name" :value="item.dValue" />
                </el-select>
            </div>
            </div>
            <div slot="footer" class="dialog-footer">
            <el-button @click="cancle">取 消</el-button>
            <el-button type="primary" @click="submit">确 定</el-button>
        </div>
    </el-dialog>
</template>
<script>
import { invitList, dealList } from "data/listBody";
export default {
  name: "systemUseradd",
  props: ["id"],
  data() {
    return {
      dialogTableVisible: true,
      invitList,
      dealList,
      roleId: "",
      name: "张三",
      invitName: "王二麻子"
    };
  },
  mounted() {
    this.dropInit();
    this.editInit();
  },
  methods: {
    dropInit() {},
    editInit() {},
    cancle() {
      this.$emit("dialogState");
    },
    submit() { //搜索关闭弹框 
      this.$emit("search");  
      this.$emit("dialogState");
    }
  }
};
</script>
<style lang="less" scoped>
.dialog-content {
  .list {
    display: flex;
    height: 40px;
    line-height: 40px;
    margin-bottom: 20px;
    span {
      display: inline-block;
      min-width: 120px;
      text-align: right;
      &.text {
        text-align: left;
      }
      i {
        display: inline-block;
        font-style: normal;
      }
    }
  }
}
</style>